.selectAndMultiselectHost {
  width: calc(50% - 20px);
  border: 1px solid #D2D2D2;
  border-radius: 8px;
  padding-left: 10px;
  align-content: center;
  margin-top: 5px;
  min-height: 55px;
  font-family: var(--app-font-quicksand-semibold), serif;
  font-size: 16px;
  font-weight: 700;
  color: var(--app-color-gray-dark);

  &::placeholder {
    color: var(--app-color-gray-light);
  }
}

.selectAndMultiselectHostTiles {
  width: calc(50% - 20px);
  border: 0px solid #D2D2D2;
  border-radius: 8px;
  padding-left: 10px;
  align-content: center;
  margin-top: 5px;
  min-height: 55px;
  font-family: var(--app-font-quicksand-semibold), serif;
  font-size: 16px;
  font-weight: 700;
  color: var(--app-color-gray-dark);

  &::placeholder {
    color: var(--app-color-gray-light);
  }
}

.section {
  display: flex;
  flex-direction: column;
}

.accordionContainer {
  overflow: hidden;
  margin: 0 0 20px 0;
  font-family: var(--app-font-quicksand-medium), serif;
  font-size: 18px;
  font-weight: 700;
  background-color: #FFFFFF;
  color: var(--app-color-gray-dark);
  padding: 10px 20px 20px 20px;
  align-items: center;
  border: 1px dashed var(--app-color-gray-light);
  border-radius: 8px;
  outline: none;
  transition: background-color 0.6s ease;

  &[app-variant-has-error="error"] {
    border: 1px solid var(--app-color-error);
  }
}

.active {
  background-color: #FFFFFF;
}

.active .title {
  font-family: var(--app-font-quicksand-medium), serif;
  font-size: 22px;
  font-weight: 700;
}

.customTitleInput {
  border: 1px solid #D2D2D2;
  border-radius: 8px;
  padding-left: 10px;
  align-content: center;
  margin-top: 5px;
  margin-bottom: auto;
  height: 37px;
  font-family: var(--app-font-quicksand-semibold), serif;
  font-size: 16px;
  font-weight: 700;
  color: var(--app-color-gray-dark);

  &::placeholder {
    color: var(--app-color-gray-light);
  }
}

.icon {
  margin-left: 20px;
  margin-top: auto;
  margin-bottom: auto;
  transition: transform 0.6s ease;
}

.loaderContainer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 32px;
  max-height: 80%;
  margin: 0 0px 0 5px;

  .host[comp-loading="loading"] & {
    transform: translateX(-5px);
  }
}

.iconsContainer {
  margin-left: auto;
  margin-top: auto;
  margin-bottom: auto;
  cursor: pointer;
}

/* Style to rotate icon when state is active */
.rotate {
  transform: rotate(180deg);
}

/* Style the accordion content panel. Note: hidden by default */
.content {
  background-color: white;
  text-align: left;
  overflow: auto;
  transition: max-height 0.6s ease;
}

.fieldTypeInput {
  ::placeholder {
    font-family: var(--app-font-quicksand-medium), serif;
    font-size: 16px;
    font-weight: 700;
    color: var(--app-color-error);
  }

  width: 100%;
  padding-left: 10px;
  border: 0px;
  height: 37px;
  font-family: var(--app-font-quicksand-semibold),
  serif;
  font-size: 20px;
  font-weight: 700;
  color: var(--app-color-gray-dark);
}

.labelInput {  
  width: 100%;  
  border: 0px;  
  font-family: var(--app-font-quicksand-semibold),
  serif;
  font-size: 16px;  
  color: var(--app-color-gray-light);
}

input {
  outline: none;
}

.fieldRowContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
  margin-bottom: auto;
  height: 50px;
  flex: 1 1;  
}

.accordion:hover,
.title {
  font-family: var(--app-font-quicksand-medium), serif;
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 0;
  line-height: 24px;
  text-align: left;
}

.emulatedFlexGap>* {
  margin: 12px 0 0 12px;
}

.emulatedFlexGap {
  padding-top: 10px;
  padding-bottom: 15px;
  display: inline-flex;
  flex-wrap: wrap;
  align-content: center;
  width: 100%;
}

.regularAccordion>* {
  margin: 12px 0 0 12px;
}

.regularAccordion {
  padding-top: 10px;
  padding-bottom: 15px;

  margin: -12px 0 0 -12px;
  width: 100%;
}

.singleInputContainer {
  border: none;
  height: auto;
  margin-top: -20px;

  input {
    cursor: pointer;
    text-align: left;
    padding-left: 15px;
    font-family: var(--app-font-quicksand-medium), serif;
    color: #4A4A4A;
    font-size: 18px;
    font-weight: 700;
    background-color: transparent;

  }
}

.inputContainerClassName {
  input {
    cursor: pointer;
    text-align: left;
    padding-left: 15px;
    font-family: var(--app-font-quicksand-medium), serif;
    color: #4A4A4A;
    font-size: 16px;
    font-weight: 700;

  }
}

.phoneIInput {
  outline: none !important;
  width: 100% !important;
  border: none !important;
  height: 37px !important;
  padding-top: 0 !important;
  padding-left: 80px !important;
  padding-bottom: 0 !important;
}

.currencySelectContainer {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 37px !important;
  border: 1px solid var(--app-color-gray-lighter);
  border-radius: 4px;
  width: 100%;
}

.currencySymbol {
  height: 37px !important;
  cursor: grab;
}

.currencySelectComponent {
  
  border: none;
  
}

.currencySelectMenuOverlayList {
  top: 80%;
}

.currencySelectMenuOverlayListItem {
  padding: 5px;
}

.currencySearchInput {
  max-width: 0%;
  align-items: center;
  color: var(--app-color-gray-dark);
  height: 90%;
}

.currencyClearControlsClassName {
  padding: 0 10px;
  display: none;
  color: var(--app-color-gray-dark);
}

.currencyInputContainerClassName {
  justify-content: flex-start;
}

.currencyInput {
  width: 100%;
  border: none;
  font-family: var(--app-font-quicksand-medium), serif;
  font-size: 20px;
  font-weight: 700;
  margin-left: 10px;
  height:37px !important;
  color: var(--app-color-gray-dark);
}

/* people select */
.peopleContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 10px;
}

.peopleBooleansOptions {
  >div {
    width: 165px;
  }
}

.peoplePositionSelectContainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  border: 1px solid var(--app-color-gray-lighter);
  border-radius: 4px;
  min-height: 57px;
  height: auto;
}

.peoplePositionsSelect {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.peoplePositionsSelectInput {
  height: 37px;
  color: var(--app-color-gray-dark);
  font-family: var(--app-font-quicksand-bold);
  font-size: 16px;
  font-weight: 700;
}

.peoplePositionsSelectMenuOverlay {
  display: flex;
  flex-direction: column;
  margin: 5px auto;
  max-height: 250px;
  width: auto;
  min-width: 100%;
  top: 100%;

}

.peoplePositionsSelectMenuOverlayItem {
  display: flex;
  align-items: center;
  padding: 10px;
  padding-left: 40px;
  font-family: var(--app-font-quicksand-bold);
  font-size: 16px;
  font-weight: 700;
  color: var(--app-color-gray-dark);

}


/* Curency select */
.currencySelectContainer {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 57px;
  border: 1px solid var(--app-color-gray-lighter);
  border-radius: 4px;
  width: 100%;
}

.currencySymbol {
  font-family: var(--app-font-quicksand-bold);
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 30px;
  color: var(--app-color-gray-dark);
  padding: 0 10px;
}

.currencySelectComponent {
  border: none;
}

.currencySelectMenuOverlayList {
  left: 0px;
  right: 0;
  top: 80%;
}

.currencySelectMenuOverlayListItem {
  padding: 5px;
}

.currencySearchInput {
  max-width: 80%;
}

.currencyClearControlsClassName {
  padding: 0 10px;
  color: var(--app-color-gray-darker);
}

.unfocusableWrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 57px;
  border: 1px solid var(--app-color-gray-lighter);
  border-radius: 4px;
  width: 100%;

  &[app-variant="error"] {
    border: 1px solid var(--app-color-red-error);
  }

  &[app-container-variant="noBorder"] {
    border: none;
  }
}

.menuOverlayList {
  left: 0;
  right: 0;
  margin-top: 5px;
  border: 0;
  border-radius: 0;
  background-color: var(--app-color-gray-lightest);

}

.menuOverlayListItem {
  padding-left: 15px;
  background-color: var(--app-color-white-lightest);
  box-shadow: 0;
  font-size: 16px;
  font-weight: 700;
  color: var(--app-color-gray-dark);
  border: 1px dashed var(--app-color-gray-lighter);
  margin: 0 10px 0 10px;
  width: 90%;
}

.menuOverlayListItemInput {
  justify-content: flex-start;
  font-family: var(--app-font-quicksand-medium), serif;
}

.menuOverlayListItemInputContainerClassName {
  width: 100%;
  border: none;
  font-size: 16px;
  font-weight: 700;
  padding-left: 10px;
}

.openSelectWithSearchHost {
  border: 0;
  border-radius: 0;
  width: 100%;
  height: 350px;
  display: flex;
  flex-direction: row;
  padding: 10px 0 10px 0;
}

.openSelectWithSearch {
  border: 2px solid var(--app-color-gray-lighter);

  label {
    font-size: 16px;
    font-weight: 700;
    color: var(--app-color-gray-dark);
  }
}

.dropdownClassContainer {
  width: 100%;
  height:37px;
  display: flex;
  margin-left: 0px;
  border: 0;
}

.dropdownHeightClassContainer {
  width: 100%;
  height: 100px;
  display: flex;
  margin-left: 0px;
  border: 0;
}
.fieldTypeTextarea {
  ::placeholder {
    font-family: var(--app-font-quicksand-medium), serif;
    font-size: 16px;
    font-weight: 700;
    color: var(--app-color-error);
  }

  width: inherit;
  padding: 0 10px 0 10px;
  border: none;
  height: 100px;
  font-family: var(--app-font-quicksand-semibold),
  serif;
  font-size: 18px;
  color: var(--app-color-gray-dark);
}

.filtersContainer{
  width: inherit;
  margin-bottom: 10px;
  border: 1px dashed var(--app-color-gray-lighter);
  padding: 10px 10px 20px 10px;
}

.dropdownSelectComponent {
  border: 2px solid var(--app-color-gray-lighter);
  display: flex;
  margin-left: 0px;
  border-radius: 4px;
}

.itemsTable {
  table {
    width: 1000px;
    border-spacing: 0;
    border-top: 1px solid #bbb;
    border-left: 1px solid #bbb;
    border-right: 1px solid #bbb;

    tr {
      :first-child {
        font-size: 16px;
        font-weight: 700;

      }

      :last-child {
        border-right: 0;
      }
    }

    th {
      :first-child {
        font-size: 18px;
        text-align: center;
      }

      text-align: center;
      color: white;
      background-color: var(--app-color-gray-dark);
      border-right: 1px solid #bbb;      
    }

    td {
      text-align: center;
      font-size: 18px;
      margin: 0;
      padding: 10px 10px 10px 10px;
      border-bottom: 1px solid #bbb;
      border-right: 1px solid #bbb;
    }
  }

  table tr:hover td {
    background: red;
    color: #fbfbfb;
    cursor: grab;
  }
  table tr:last-child:hover>td {
     background: transparent;
     color: var(--app-color-gray-dark);
  }
}

.customMenuOverlayListItemCheckboxCheckedMark {
  display: block;
  margin: auto;
  color: var(--app-color-white-lightest);
  fill: var(--app-color-white-lightest);
}

.customMenuOverlayListItemNormalCheckedMark {
  margin-left: 8px;
  width: 16px;
  height: 16px;
  cursor: pointer;
  margin: 0px 10px 0px 8px;
}
.checkbox {
  margin: 8px 2px 0 0;
  :checked {
    accent-color: red;
    background-color: red;
  }
}